import { foldedLog, MSG, RED_BOLD } from "./global.js";

const ftrs = [
    '<a href="https://dev.jdeko.me/">jdeko.me</a>',
    'created & maintained by Justin DeKock',
    'contact me at: <a href="mailto:jdekock17@gmail.com">jdekock17@gmail.com</a>',
];
    
document.addEventListener('DOMContentLoaded', async () => {
    foldedLog(`%cbuilding footer`, MSG);
    const ftr = document.getElementById('ftr');
    if (!ftr) {
        foldedLog(`%cftr el not found`, RED_BOLD);
        return;
    }
    for (const ftrHTML of ftrs) {
        const d = document.createElement('div');
        d.innerHTML = ftrHTML;
        ftr.appendChild(d);
    }
});